*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}
.content{
  height: 100vh;
}
.navbar{
  background-color: #0000006c !important;
}
.register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}
/* transaction page */
.filters{
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 15px 20px;
   box-shadow: 0 0 3px gray;
}

/* antd icons */
.anticon svg{
  font-size: 20px;
  cursor: pointer;
}
.switch-icons{
  border: 1px solid rgba(0, 0, 0, 0.164);
  border-radius: 5px;
  padding:10px 15px;
}
.active-icon{
  color: black;
}
.inactive-icon{
  color: grey;
}
